import * as React from "react"
import Layout from "../components/layout"
import RaceAreaLink from "../components/race_area_link"
import { graphql } from "gatsby"

export default function Forecasts({data}) {
  return (
    <Layout>
      <h2>mm/dd</h2>
      <h3>中央競馬</h3>
      {data.allTodayRaceJson.edges.map(edge =>
        <RaceAreaLink key={edge.node.area} post={edge.node} />
      )}

    </Layout>
  )
}

export const query = graphql`
  query allTodayRaceJson {
      allTodayRaceJson {
        edges {
          node {
            area
            title
            count
          }
        }
      }
    }
`